body { background: #FFF; font-size: 50px; font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600))); line-height: 1.4; }

@media (max-width: 600px) { body { font-size: 16px; } }

@media (min-width: 1000px) { body { font-size: 50px; } }

html { background-color: #fff; -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

body { font-family: NeoSansProRegular; }

.header { background-color: #eee; width: 100%; height: 120px; border-top: 5px solid #8c363f; }

.header__container { display: flex; width: 100%; max-width: 1000px; margin: 0 auto; padding: 5px 10px; height: 120px; }

.header__title { width: 100%; text-align: center; font-size: 16px; font-weight: bold; color: black; margin: auto; }

.header__title-link { color: black; text-decoration: none; }

.span { color: #8c363f; font-size: 1.2em; }

@media only screen and (max-width: 480px) { .span { display: none; } }

.logo__image { border-radius: 100px; }

.logo__left { padding-right: 10px; }

.logo__right { padding-left: 10px; }

.hero { width: 100%; height: 500px; background-color: #eee; }

@media only screen and (max-width: 1000px) { .hero { height: 100%; } }

@media only screen and (max-width: 480px) { .hero { display: none; } }

.hero__container { position: relative; }

.nav { z-index: 999999; position: relative; margin: 0 auto; width: 840px; transition: top ease-out 0.6s; }

.nav__list { border-top: 3px solid #8c363f; position: absolute; margin-top: 35px; display: flex; justify-content: center; }

.nav__link { background-color: #eee; padding: 10px; width: 140px; height: 60px; text-decoration: none; color: #000; font-size: 18px; font-weight: 600; line-height: 25px; display: table-cell; text-align: right; vertical-align: bottom; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); }

.nav__link:hover { background-color: #eee; color: #8c363f; height: 110px; transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225); }

@media only screen and (max-width: 1000px) { .nav__link { height: 50px; font-size: 16px; width: 115px; padding: 7px; } }

@media only screen and (max-width: 720px) { .nav__link { height: 40px; padding: 5px; font-size: 14px; min-width: 100px; } }

.nav__current { background-color: #8c363f; color: #eee; height: 110px; }

@media only screen and (max-width: 1000px) { .nav__current { height: 100px; } }

.nav-fixed { z-index: 999999; position: relative; }

.nav-fixed__list { max-width: 1000px; position: fixed; top: 0; left: 0; right: 0; border-top: 3px solid #8c363f; margin: auto; transition: all .9s ease; display: flex; background-color: #eee; justify-content: center; -webkit-box-shadow: 0px 3px 8px 1px rgba(0, 0, 0, 0.6); }

@media only screen and (max-width: 720px) { .nav-fixed__list { flex-wrap: wrap; } }

.nav-fixed__link { max-width: 200px; width: 16%; min-width: 120px; padding: 5px; text-decoration: none; background-color: #eee; color: #000; font-size: 18px; font-weight: 600; display: table-cell; text-align: center; vertical-align: bottom; transition: all .4s ease; }

.nav-fixed__link:hover { background-color: #eee; color: #8c363f; }

@media only screen and (max-width: 1000px) { .nav-fixed__link { font-size: 16px; font-weight: 500; max-width: auto; } }

@media only screen and (max-width: 720px) { .nav-fixed__link { font-size: 14px; font-weight: 400; flex-wrap: wrap; width: 50%; max-width: auto; } }

@media only screen and (max-width: 580px) { .nav-fixed__link { flex-wrap: wrap; width: 50%; } }

.nav-fixed__current { background-color: #8c363f; color: #eee; }

.carousel { position: relative; height: 500px; width: 100%; }

@media only screen and (max-width: 1000px) { .carousel { height: auto; } }

.carousel__container { margin: 0 auto; max-width: 1920px; width: 100%; }

.carousel__img { width: 100%; height: 500px; }

@media only screen and (max-width: 1000px) { .carousel__img { height: auto; } }

.arrow { position: absolute; font-family: "Cooper Black"; cursor: pointer; z-index: 1; color: #eee; opacity: 1; font-size: 80px; width: 100px; top: 0; bottom: 0; background: rgba(255, 255, 255, 0); transition: all .5s ease; }

@media only screen and (max-width: 1000px) { .arrow { display: none; } }

.arrow:hover { color: #8c363f; text-shadow: #000 1px 1px 5px; background: rgba(255, 255, 255, 0.3); }

.arrow__prev { left: 0; }

.arrow__next { right: 0; }

.slick-buttons button li { font-size: 50px; }

.content { width: 100%; background: #eee; }

.content__container { position: relative; width: 1000px; margin: 0 auto; margin-top: -300px; display: flex; flex-wrap: wrap; background: #eee; }

@media only screen and (max-width: 1000px) { .content__container { width: 100%; top: 0; margin-top: 0; } }

.content__container-main { position: relative; width: 1000px; margin: 0 auto; margin-top: -100px; display: flex; flex-wrap: wrap; background: #eee; }

@media only screen and (max-width: 1000px) { .content__container-main { width: 100%; } }

@media only screen and (max-width: 480px) { .content__container-main { margin-top: 0; } }

.content__center { width: 100%; padding: 10px; text-align: justify; }

.content__left { width: 50%; padding: 10px; text-align: justify; }

@media only screen and (max-width: 720px) { .content__left { width: 100%; } }

.content__right { width: 50%; padding: 10px; text-align: justify; }

@media only screen and (max-width: 720px) { .content__right { width: 100%; } }

.content__name { font-size: 22px; margin-bottom: 10px; }

.footer { background-color: #232528; width: 100%; border-top: 5px solid #8c363f; }

.footer__container { display: flex; flex-wrap: wrap; width: 1000px; margin: 0 auto; }

@media only screen and (max-width: 1000px) { .footer__container { flex-direction: column-reverse; width: 100%; } }

.footer__copyright { width: 50%; min-width: 300px; padding: 10px; color: #eee; font-size: 16px; }

@media only screen and (max-width: 1000px) { .footer__copyright { width: 100%; text-align: center; } }

.footer__social { width: 50%; padding: 10px; min-width: 200px; margin: 0 auto; text-align: center; }

.footer__social-item { display: inline-block; }

.person { width: 100%; }

.person__list { display: flex; flex-wrap: wrap; }

.person__item { border: 2px solid rgba(140, 54, 63, 0.1); width: 100%; margin-bottom: 10px; }

.person__item:last-child { margin-bottom: 0; }

.person__item:hover { border: 2px solid #8c363f; background: rgba(140, 54, 63, 0.1); }

.person__photo { width: 200px; height: 250px; float: left; margin: 10px; }

.person__info { font-size: 16px; line-height: 16px; font-weight: 600; line-height: 24px; }

.person__name { font-size: 20px; line-height: 18px; font-weight: 800; line-height: 32px; }

.person__name-link { text-decoration: none; color: #8c363f; font-size: 20px; line-height: 36px; }

.person__name-link:hover { text-decoration: underline; }

.person__mail { font-size: 16px; line-height: 16px; font-weight: 600; line-height: 32px; text-decoration: none; color: #8c363f; }

.person__mail-link { text-decoration: none; color: #8c363f; font-size: 18px; line-height: 36px; font-weight: 600; }

.person__mail-link:hover { text-decoration: underline; }

.person__site { font-size: 18px; }

.person__site-link { text-decoration: none; color: #8c363f; font-size: 18px; line-height: 36px; font-weight: 600; }

.person__site-link:hover { text-decoration: underline; }

.person__item-25 { width: 20%; margin-right: 10px; flex-wrap: nowrap; }

.person__item-25:nth-child(4n) { margin-right: 0; }

.person__item-50 { width: 50%; margin-bottom: 10px; margin-right: 10px; }

.person__item-50:nth-of-type(3n) { margin-right: 0; }

.person__item-100 { width: 100%; margin-bottom: 10px; }

.event { width: 100%; }

.event__item { opacity: 0; width: 100%; border: 2px solid rgba(140, 54, 63, 0.1); padding: 0 10px 10px 10px; margin-bottom: 10px; transform: translate(0px, 100px); transition: all 500ms linear; }

.event__item:last-child { margin-bottom: 0; }

.event__item:hover { border: 2px solid #8c363f; background: #eee; }

.event__item:hover .event__date { background: #8c363f; border-right: 2px solid #8c363f; border-bottom: 2px solid #8c363f; }

.event__item:hover .event__date-day { color: #eee; }

.event__item:hover .event__date-month { color: #eee; }

.event__date { float: left; margin-right: 10px; margin-left: -10px; width: 75px; height: 75px; border-right: 2px solid rgba(140, 54, 63, 0.1); border-bottom: 2px solid rgba(140, 54, 63, 0.1); }

.event__date-day { color: #8c363f; text-align: center; font-size: 30px; font-weight: 600; }

.event__date-month { color: #8c363f; text-align: center; font-size: 20px; font-weight: 600; text-transform: uppercase; }

.event__name { font-size: 18px; font-weight: 600; margin-bottom: 10px; }

.event__name-link { color: #8c363f; text-decoration: none; }

.event__name-link:hover { text-decoration: underline; }

.event__description { font-size: 16px; font-weight: 400; line-height: 22px; text-align: justify; }

.event__visible { transition: all 400ms linear; opacity: 1; transform: translate(0px, 0px); }
